@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
* {
  font-family: "Roboto Slab", serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  background: linear-gradient(to left, #4CB2D4, #7828FD);
  width: 100%;
  height: 100vh;
}
.login {
  background: url(/website/static/media/background.5d58edd2.jpg);
  max-height: 500px;
  background-size: cover;
  color: white;
}
.login {
  border-radius: 20px;
