  width: 400px;
  height: 440px;
  margin-left: auto;
  margin-top: 100px;
  margin-right: auto;
  margin-bottom: auto;
  box-sizing: border-box;
  box-shadow: 8px 8px 50px lightgreen;
  padding: 60px 30px;
  position: absolute;
  -webkit-transform: translate(-50% -50%);
          transform: translate(-50% -50%);
}
.login input {
  width: 100%;
  margin-bottom: 20px;
}
.login input[type="text"],
.login input[type="password"] {
  border: none;
  border-bottom: 1px solid #ddd;
  background: transparent;
  outline: none;
  height: 30px;
  opacity: 1;
  color: #ccc;
  font-size: 17px;
}

.signUp-text {
