}
.box div {
  position: absolute;
  width: 60px;
  height: 60px;
  background-color: transparent;
  border: 6px solid rgba(255, 255,255, 0.8);
}
.box div:nth-child(1) {
  top: 12%;
  left: 42%;
  -webkit-animation: animate 10s linear infinite;
          animation: animate 10s linear infinite;
}
.box div:nth-child(2) {
  top: 70%;
  left: 50%;
  -webkit-animation: animate 7s linear infinite;
          animation: animate 7s linear infinite;
}
.box div:nth-child(3) {
  top: 17%;
  left: 6%;
  -webkit-animation: animate 9s linear infinite;
          animation: animate 9s linear infinite;
}
.box div:nth-child(4) {
  top: 20%;
  left: 60%;
  -webkit-animation: animate 10s linear infinite;
          animation: animate 10s linear infinite;
}
.box div:nth-child(4) {
  top: 20%;
  left: 60%;
  -webkit-animation: animate 10s linear infinite;
          animation: animate 10s linear infinite;
}
.box div:nth-child(5) {
  top: 67%;
  left: 10%;
  -webkit-animation: animate 6s linear infinite;
          animation: animate 6s linear infinite;
}
.box div:nth-child(6) {
  top: 80%;
  left: 90%;
  -webkit-animation: animate 12s linear infinite;
          animation: animate 12s linear infinite;
}
.box div:nth-child(7) {
  top: 30%;
  left: 90%;
  -webkit-animation: animate 12s linear infinite;
          animation: animate 12s linear infinite;
}
@-webkit-keyframes animate{
  0% {
    -webkit-transform: scale(0) translate(0) rotate(0) ;
            transform: scale(0) translate(0) rotate(0) ;
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.3) translate(-90px) rotate(360deg);
            transform: scale(1.3) translate(-90px) rotate(360deg);
    opacity: 0;
  }
}
@keyframes animate{
  0% {
    -webkit-transform: scale(0) translate(0) rotate(0) ;
            transform: scale(0) translate(0) rotate(0) ;
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.3) translate(-90px) rotate(360deg);
            transform: scale(1.3) translate(-90px) rotate(360deg);
    opacity: 0;
  }
}


.square {
  width: 400px;
  height: 400px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
  position: relative;
}
.square span:nth-child(1) {
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 2px solid;
  border-radius: 38% 62% 63% 37%/ 41% 44% 56% 59%;
  transition: 1s;
  -webkit-animation: animate 6s linear infinite;
          animation: animate 6s linear infinite;
}
.square:hover span:nth-child(1) {
  border: none;
  background: limegreen;
}
.square span:nth-child(2) {
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 2px solid;
  border-radius: 38% 62% 63% 37%/ 41% 44% 56% 59%;
  transition: 1s;
  -webkit-animation: animate2 4s linear infinite;
          animation: animate2 4s linear infinite;
}

.square:hover span:nth-child(2) {
  border: none;
  background: limegreen;
}
.square span:nth-child(3) {
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 2px solid;
  border-radius: 38% 62% 63% 37%/ 41% 44% 56% 59%;
  transition: 1s;
  -webkit-animation: animate 10s linear infinite;
          animation: animate 10s linear infinite;
}

.square:hover span:nth-child(3) {
  border: none;
  background: limegreen;
}
@keyframes animate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  0% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes animate2 {
  0% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}
@keyframes animate2 {
  0% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}
